.searched-item {
  width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 40px 0 0;
  &__image {
    a {
      text-decoration: none;
    }
    img {
      width: 200px;
      height: 300px;
      object-fit: contain;
    }
  }
  &__name {
    margin: 0 0 20px 0;
    a {
      text-decoration: none;
      h2 {
        font-family: $OpenSans;
        font-size: 16px;
        font-weight: 400;
        text-align: center;
        color: #000;
      }
    }
  }
  &__price {
    a {
      text-decoration: none;
      span {
        font-family: $OpenSans;
        font-weight: 400;
        color: #000;
      }
    }
  }
}